<template>
  <div class="tab-control">
    <van-tabs v-model="active" @click="tabItemClick">
      <van-tab v-for="item in brotherCategory" :key="item.id">
        <template #title>{{item.name}}</template>
        <h2 class="name">{{item.name}}</h2>
        <p class="front-name">{{item.front_name}}</p>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  name:"TabControl",
  data(){
    return{
      id:'',
      active:0
    }
  },
  props:{
    brotherCategory:{
      type:Array,
      default(){
        return []
      }
    }
  },
  methods:{
    tabItemClick(index){
      this.$emit('tabItemClick',index);
    }
  },
  watch:{
    brotherCategory:function(){
      // console.log('111111111111',this);
      // console.log('this.brotherCategory=>',this.brotherCategory);
      if(this.brotherCategory.length){
        this.brotherCategory.forEach((item,index)=>{
          if(this.id == item.id){
            this.active = index;
          }
        })
      }else{
        this.active = 0;
      }
    }
  },
  created(){
    this.id = this.$route.params.id;
    
  }
}
</script>

<style lang="less" scoped>
.tab-control {
  background-color: #fff;
  text-align: center;
  .name {
    font-size: 0.18rem;
    padding: 0.12rem 0;
    font-weight: 600;
  }
  .front-name {
    font-size: 0.14rem;
    padding: 0.08rem 0.12rem;
  }
}
</style>