<template>
  <!-- 当前频道的所有商品 -->
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <div class="goods">
      <ul class="goods-list">
        <li v-for="item in goodsList" :key="item.id" class="goods-item">
          <img v-lazy="item.list_pic_url" :alt="item.name" />
          <p class="name">{{item.name}}</p>
          <div class="price">{{item.retail_price | priceFormat}}</div>
        </li>
      </ul>
    </div>
  </van-pull-refresh>
</template>

<script>
export default {
  name:"Goods",
  props:{
    goodsList:{
      type:Array,
      default(){
        return {}
      }
    }
  },
  data(){
    return{
      isLoading:false
    }
  },
  methods:{
    onRefresh(){
      this.$emit('refresh',this.changeLoadingState);
    },
    changeLoadingState(isLoading){
      console.log('loadingCallBack');
      this.isLoading = isLoading;
      
    }
  }
}
</script>

<style lang="less" scoped>
.goods {
  margin: 0.1rem 0.06rem;
  ul.goods-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  li.goods-item {
    width: 49%;
    background-color: #fff;
    margin-bottom: 0.1rem;
    border-radius: 0.06rem;
    text-align: center;
    img {
      width: 100%;
    }
    p.name {
      font-size: 0.14rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      padding: 0 4px;
      line-height: 0.3rem;
    }
    div.price {
      font-size: 0.12rem;
      color: #8b0000;
      line-height: 0.24rem;
    }
  }
}
</style>