<template>
  <div id="channel">
    <!-- 顶部的tabbar栏 -->
    <TopBar @goBack="handleBack" />

    <!-- tabcontrol -->
    <TabControl :brotherCategory="brotherCategory" @tabItemClick="handleTabItemClick" />

    <!-- 当前频道的所有商品 -->
    <Goods :goods-list="goodsList" @refresh="handleRefresh" />
    <BottomTip />
  </div>
</template>

<script>
import {getChannelById,getChannelGoodsById} from "@/network/apis"
import BottomTip from '@/components/content/BottomTip'
import TopBar from '@/components/content/TopBar'
import Goods from './childComps/Goods'
import TabControl from './childComps/TabControl'
export default {
  name:"Channel",
  data(){
    return{
      // 当前所在的频道(category)id
      id:'',
      brotherCategory:[],
      currentCategory:{},
      goodsList:[],
      isLoading:false,
      loadingCallBack:null
    }
  },
  components:{
    BottomTip,TopBar,Goods,TabControl
  },
  methods:{
    getChannelById(params){
      getChannelById(params).then(res=>{
        console.log('getChannel==>',res);
        if(res.errno === 0){
          let {brotherCategory,currentCategory} = res.data;
          this.brotherCategory = brotherCategory;
          this.currentCategory = currentCategory;
        }
      })
    },
    //  ?categoryId=1005000&page=1&size=1000
    getChannelGoodsById(params){
      getChannelGoodsById(params).then(res=>{
        // console.log('getChannelGoodsById==>',res);
        this.isLoading = false;
        if(res.errno === 0){
          let {goodsList} = res.data;
          this.goodsList = goodsList;
        }
        // 通知下拉刷新控件
        if(this.loadingCallBack) this.loadingCallBack(false);
      })
    },
    // 事件处理
    handleBack(){
      this.$router.back();
    },
    handleTabItemClick(index){
      this.id =  this.brotherCategory[index].id;
      // console.log('Channel,tabItemClick==>',index,this.id);
      this.$router.replace(`/channel/${this.id}`);
      // 切换频道,请求对应频道数据
      this.getChannelGoodsById({
        categoryId:this.id,
        page:1,
        size:1000
      });
    },
    handleRefresh(callBack){
      this.loadingCallBack = callBack;
      this.loadingCallBack(true);
      this.getChannelGoodsById({
        categoryId:this.id,
        page:1,
        size:1000
      });
    }

  },
  created(){
    //  获取动态路由的的参数 /channel/id ==> /channel/1005001
    let {id} = this.$route.params;
    this.id = id;
    // console.log('Channel，id==>',id);
    // 请求所有频道数据
    this.getChannelById({id});
    //请求当前频道的商品数据
    this.getChannelGoodsById({
      categoryId:this.id,
      page:1,
      size:1000
    });
  }
}
</script>

<style scoped lang="less">
#channel {
  background-color: #efefef;
  font-size: 0.14rem;
  margin-bottom: 0.5rem;
}
</style>